'use client';

import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { Input } from '@/components/ui/Input';
import { Select } from '@/components/ui/Select';
import { Checkbox } from '@/components/ui/Checkbox';

export default function Settings() {
  return (
    <div className="space-y-6 max-w-2xl">
      <div>
        <h1 className="text-3xl font-bold text-gray-900 dark:text-white">System Settings</h1>
        <p className="text-gray-600 dark:text-gray-400 mt-2">
          Configure system-wide evaluation settings
        </p>
      </div>

      {/* General Settings */}
      <Card>
        <CardHeader>
          <CardTitle>General Settings</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <Input label="Institution Name" value="College of Information Technology" />
          <Input label="Institution Code" value="CIT" />
          <Select
            label="Academic Calendar Type"
            options={[
              { value: 'semester', label: 'Semester' },
              { value: 'trimester', label: 'Trimester' },
            ]}
            defaultValue="semester"
          />
          <Button variant="primary">Save Changes</Button>
        </CardContent>
      </Card>

      {/* Evaluation Settings */}
      <Card>
        <CardHeader>
          <CardTitle>Evaluation Settings</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <Checkbox label="Allow anonymous evaluations" defaultChecked />
          <Checkbox label="Require evaluation comments" defaultChecked />
          <Checkbox label="Show CHED compliance indicator" defaultChecked />
          <Checkbox label="Enable AI feedback analysis" />
          
          <div className="pt-4">
            <Select
              label="Evaluation Scale"
              options={[
                { value: '5', label: '5-point scale (1-5)' },
                { value: '10', label: '10-point scale (1-10)' },
                { value: '4', label: '4-point scale (1-4)' },
              ]}
              defaultValue="5"
            />
          </div>

          <Button variant="primary">Save Changes</Button>
        </CardContent>
      </Card>

      {/* Notification Settings */}
      <Card>
        <CardHeader>
          <CardTitle>Notification Settings</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <Checkbox label="Send deadline reminders" defaultChecked />
          <Checkbox label="Notify on evaluation submission" defaultChecked />
          <Checkbox label="Send completion reports" />
          <Checkbox label="Alert on low completion rate" defaultChecked />

          <Button variant="primary">Save Changes</Button>
        </CardContent>
      </Card>

      {/* System Maintenance */}
      <Card className="border-yellow-200 dark:border-yellow-800 bg-yellow-50 dark:bg-yellow-900/20">
        <CardHeader>
          <CardTitle>System Maintenance</CardTitle>
        </CardHeader>
        <CardContent className="space-y-3">
          <p className="text-sm text-gray-700 dark:text-gray-300">
            Perform system maintenance tasks
          </p>
          <div className="flex gap-2">
            <Button variant="outline">Backup Data</Button>
            <Button variant="outline">Clear Cache</Button>
            <Button variant="danger">Archive Old Evaluations</Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
